.burger {
  position: fixed;
  top: 0;
  right: 17px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  visibility: hidden;
  height: 55px;
  width: 55px;
  transition: 0.4s;
  z-index: 15;
}

.burger:hover {
  background-color: #77b900;
}

.burger__wrapp {
  height: 17px;
  width: 25px;
  transition: all 0.4s;
  position: relative;
}

.burger__line {
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: #fff;
  top: 0;
  left: 0;
  transition: all 0.4s;
}

.burger__line:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line:nth-child(3) {
  bottom: 0;
  top: initial;
}

.burger--active .burger__line:nth-child(1) {
  opacity: 0;
}

.burger--active .burger__line:nth-child(2) {
  transform: rotate(45deg);
}

.burger--active .burger__line:nth-child(3) {
  transform: rotate(-45deg) translateY(-4px) translateX(5px);
}

.cross {
  position: absolute;
  right: 8px;
  top: 20px;
  height: 17px;
  width: 25px;
  cursor: pointer;
  z-index: 15;
}

.cross__line {
  width: 100%;
  height: 2px;
  display: block;
  background-color: #fff;
}

.cross__line:nth-child(1) {
  transform: rotate(45deg);
}

.cross__line:nth-child(2) {
  transform: rotate(-45deg) translateX(1px) translateY(-1px);
}

.desc-list {
  display: flex;
  margin-bottom: 50px;
}

.desc-list__content {
  text-align: left;
}

.desc-list__item {
  margin-right: 30px;
  flex: 1;
}

.desc-list__item:last-child {
  margin-right: 0;
}

.desc-list__icon {
  margin-bottom: 25px;
}

.desc-list__subtitle {
  font-size: 26px;
  line-height: 36px;
  color: #77b900;
  margin-bottom: 11px;
}

.desc-list__subtitle span {
  color: #fff;
}

.desc-list__text {
  font-family: "Roboto";
  font-size: 16px;
  line-height: 28px;
  color: #fff;
}

.fullmenu {
  position: fixed;
  top: 0;
  left: 100%;
  opacity: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  background-color: black;
  transition: 0.4s;
}

.fullmenu__content {
  height: 100%;
  width: 100%;
  position: relative;
}

.fullmenu__menu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.fullmenu__menu .menu__list {
  flex-direction: column;
  align-items: center;
}

.fullmenu__menu .menu__item {
  margin-right: 0;
  margin-bottom: 10px;
}

.fullmenu__menu .menu__item:last-child {
  margin-bottom: 0;
}

.fullmenu__socials {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__col {
  display: flex;
  align-items: center;
}

.logo {
  margin-right: 38px;
}

.logo__link {
  display: inline-block;
}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__item {
  margin-right: 51px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  font-family: "Roboto";
  text-transform: uppercase;
  font-size: 16px;
  color: #fff;
  transition: 0.3s;
}

.menu__link:hover {
  color: grey;
}

.popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  visibility: hidden;
  padding: 69px 93px 51px;
  max-width: 540px;
  background: #000000;
  border: 2px solid #77b900;
  box-shadow: 0px 0px 20px rgba(119, 185, 0, 0.5);
}

.popup__content {
  text-align: center;
}

.popup__title {
  font-weight: 700;
  font-size: 26px;
  line-height: 36px;
  margin-bottom: 20px;
  color: #fff;
}

.popup__inputs {
  margin-bottom: 20px;
}

.popup__inputs input {
  margin-bottom: 20px;
}

.popup__inputs input:last-child {
  margin-bottom: 0;
}

.popup__button {
  text-align: center;
}

.active {
  visibility: visible;
}

.preview {
  text-align: center;
}

.preview__title {
  margin-bottom: 24px;
}

.preview__text {
  font-family: "Roboto";
  font-size: 16px;
  line-height: 28px;
  color: #fff;
  max-width: 65%;
  display: inline-block;
  margin-bottom: 49px;
}

.preview__button {
  text-align: center;
}

.socials__list {
  display: flex;
  align-items: center;
}

.socials__item {
  margin-right: 10px;
}

.socials__item:last-child {
  margin-right: 0;
}

* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: "Play";
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  border: 2px solid transparent;
  cursor: pointer;
}

input {
  outline: none;
  border: 1px solid transparent;
}

.wrapper {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: black;
  background: url(../img/bg/bg.png) center no-repeat;
  background-size: cover;
}

.container {
  width: 1100px;
  margin: 0 auto;
  max-width: 100%;
}

.title {
  font-weight: 700;
  font-size: 26px;
  letter-spacing: 0.25px;
  color: #fff;
}

.main {
  margin-top: 75px;
}

.button {
  padding: 16px 57px;
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  background: #77b900;
  transition: 0.4s;
}

.button:hover {
  background: transparent;
  box-shadow: 0px 0px 10px #77b900;
  border-color: #77b900;
}

.input {
  width: 100%;
  padding: 13px 20px;
  font-family: "Roboto";
  font-size: 16px;
  line-height: 28px;
  letter-spacing: 0.3px;
  color: #000000;
}

.fullmenu--active {
  left: 0;
  opacity: 1;
}

.body--active-menu {
  overflow: hidden;
}

@media screen and (max-width: 850px) {
  .popup {
    padding: 69px 40px 51px;
  }
}

@media screen and (max-width: 768px) {
  .desc-list {
    flex-direction: column;
  }

  .desc-list__content {
    text-align: center;
  }

  .desc-list__item {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .desc-list__item:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 650px) {
  .burger {
    visibility: visible;
  }

  .header__menu,
  .header__socials {
    display: none;
  }

  .popup {
    padding: 50 40px;
  }
}